body {
  background: rgb(178, 122, 195);
  background-image: url(assets/image/bg.png);


}

.wrapper {
  max-width: 1600px;
  margin: 2px auto;

  @media (max-width: 645px) {
    width: calc(100vw - 30px);
  }

  .header {
    display: flex;
    grid-template-columns: 50px 1fr 50px;
    justify-content: space-between;
    background: rgba(47, 3, 60, 0.786);

    margin: auto;
    border-radius: 30px;

    @media (max-width: 940px) {
      width: 100%;
    }

    @media (max-width: 400px) {
      width: 100%;
    }

    .header_logo {
      cursor: pointer;
    }

    .header_title {
      display: flex;
      max-height: 180px;

    }

    .header_name {
      font-family: 'BOMBFONT TRIAL';
      font-size: 120px;
      color: rgb(255, 255, 255);
      height: 50px;
      vertical-align: top;
      margin-top: -20px;

      @media (max-width: 1170px) {
        font-size: 90px;
      }

      @media (max-width: 940px) {
        font-size: 70px;
      }

      @media (max-width: 800px) {
        font-size: 50px;
        margin-top: 0px;
      }

    }

    .header_discribe {
      font-family: 'Streetlife';
      font-size: 50px;
      height: 30px;
      vertical-align: top;
      color: rgb(178, 122, 195);

      @media (max-width: 600px) {
        padding-top: 20px;
        font-size: 35px;

      }

    }

    .cart {
      padding-top: 20px;
      padding-right: 30px;
      color: #fff;
      background: rgba(47, 3, 60, 0.786);
      border-radius: 30px;
      width: 120px;
      margin: 5px;
      cursor: pointer;

      .cart_button {
        justify-content: center;
        padding-left: 30px;
      }

      .cart_button img:hover {
        transform: translate(-20px, 0);
      }
    }
  }

  .category {
    max-width: 1600px;
    height: 58px;
    background: rgba(47, 3, 60, 0.786);
    border-radius: 30px;
    color: #fff;
    display: flex;
    justify-content: space-between;
    margin: auto;
    margin-top: 2px;
    margin-bottom: 2px;


    li {
      list-style-type: none;

    }

    .categories {
      display: flex;
      margin: 2px;
      padding: 2px;
      z-index: 100;

      .category_active {
        background: rgb(178, 122, 195);
        border-radius: 30px;
        display: block;
        text-align: center;
        width: 100px;
        height: 30px;
        padding: 10px;
        cursor: pointer;
      }

      .category_name {
        background: rgba(47, 3, 60, 0.786);
        border-radius: 30px;
        display: block;
        text-align: center;
        width: 100px;
        height: 30px;
        padding: 10px;
        cursor: pointer;
      }
    }
  }

  .sort {
    position: relative;
    background: rgba(47, 3, 60, 0.786);
    border-radius: 30px;
    display: block;
    text-align: center;
    width: 220px;
    height: 30px;
    padding: 10px;
    margin: 4px;
    font-size: 12px;
    z-index: 100;

    &__label {
      display: flex;
      align-items: center;

      svg {
        margin-right: 8px;
      }

      b {
        margin-right: 8px;
      }

      span {
        color: rgba(250, 250, 250, 0.786);
        border-bottom: 1px dashed rgba(47, 3, 60, 0.786);
        font-size: 14px;
        cursor: pointer;
      }
    }

    &__popup {
      position: absolute;
      right: 0;
      margin-top: 15px;
      background: rgba(47, 3, 60, 0.786);
      box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
      border-radius: 10px;
      overflow: hidden;



      ul {
        overflow: hidden;

        li {

          padding: 12px 20px;
          cursor: pointer;

          &.active,
          &:hover {
            background: rgba(254, 95, 30, 0.05);
          }

          &.active {
            font-weight: bold;
            color: rgba(47, 3, 60, 0.786);
          }
        }
      }
    }
  }


  .content {
    border-radius: 30px;
    display: grid;
    grid-template-columns: repeat(4, 400px);
    justify-content: center;
    max-width: 1600px;
    background: rgba(47, 3, 60, 0.786);
    margin: auto;

    @media (max-width: 1599px) {
      grid-template-columns: repeat(3, 1fr);
      margin: 0;
      padding: 0;
    }

    @media (max-width: 1200px) {
      grid-template-columns: repeat(2, 1fr);
      margin: 0;
      padding: 0;
    }

    @media (max-width: 800px) {
      grid-template-columns: repeat(1, 1fr);
      margin: 0;
      padding: 0;
    }

    .card {
      background-image: url(assets/image/shelf1.png);
      width: 400px;
      height: 400px;
      position: relative;


      .cart_price {
        color: #fff;
      }

      .item {
        position: absolute;
        bottom: 100px;
        padding-left: 30px;

      }

      .item img {
        max-width: 280px;
        cursor: pointer;
      }

      .item img:hover {
        max-width: 420px;

      }

      .item_name {
        position: absolute;
        bottom: 60px;
        left: 12px;
        font-size: 18px;
        color: aliceblue;
      }

      .price {
        background-image: url(assets/image/price.png);
        width: 100px;
        height: 70px;
        position: absolute;
        bottom: 25px;
        right: 12px;
        transform: skew(-18deg, 18deg);


        .price_property {
          padding-left: 15px;
          padding-top: 10px;
        }

        span {
          color: rgb(86, 22, 10);
          font-size: 18px;
        }

        .price_add {

          margin-left: 50px;
          cursor: pointer;
        }

        .price_add img {
          max-height: 20px;
          max-width: 20px;
        }

        .price_add img:hover {
          transform: scale(1.2)
        }
      }

      .price:hover {
        transform: skew(0);
      }
    }
  }
}

.cart_content {
  max-width: 1600px;
  margin: 0 auto;
  background: rgba(47, 3, 60, 0.786);
  border-radius: 30px;
  display: block;
  text-align: center;
  color: #fff;
  // .cart_header {



  // }
  .cart_items {

    .cart_item img {
      max-width: 150px;
    }

  }
}

.search_block {


  z-index: 100;
  padding-left: 10px;
  margin: 3px;

  input {
    background: rgba(47, 3, 60, 0.786);
    border: none;
    border-radius: 30px;
    color: #fff;
    width: 250px;
    height: 50px;
    font-size: 16px;
    padding-left: 18px;

  }

  .close_img {
    margin-left: -30px;
  }
}

.pagination {
  margin: 0 auto;

  li {
    display: inline-block;


    a {
      text-align: center;
      width: 45px;
      line-height: 42px;
      height: 45px;
      border: 1px solid rgba(47, 3, 60, 0.786);
      border-radius: 30px;
      margin-left: 10px;
      display: inline-block;
      cursor: pointer;
      color: #fff;

      &:hover {
        background-color: rgba(47, 3, 60, 0.786);
        color: #fff;
      }
    }
  }

  :global {
    .selected {
      a {
        background-color: rgba(47, 3, 60, 0.786);
        color: #fff;
      }
    }

  }
}

.card_full {
  background-color: rgba(47, 3, 60, 0.786);
  width: 1600px;
  height: 700px;
  border-radius: 30px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  margin-top: 10px;

  .cart_price_full {
    color: #fff;
  }

  .item_full {
    grid-area: 1 / 1 / 2 / 2;
  }



  .item_full {
    background: rgba(47, 3, 60, 0.786);
    border-radius: 30px;
    width: 600px;
    margin: 5px;

  }

  .item_full img {
    width: 450px;
    margin-left: 50px;
  }

  .card_full_description {
    background: rgba(47, 3, 60, 0.786);
    border-radius: 30px;
    width: 980px;
    font-size: 18px;
    color: #fff;
    grid-area: 1 / 2 / 2 / 3;
    margin: 5px;
  }



  .item_name_full {
    background: rgba(47, 3, 60, 0.786);
    border-radius: 30px;
    color: #fff;
    left: 12px;
    font-size: 22px;
    width: 600px;
    max-height: 70px;
    text-align: center;
    grid-area: 2 / 1 / 3 / 2;

  }

  .price_full {
    background: rgba(47, 3, 60, 0.786);
    border-radius: 30px;
    width: 980px;
    height: 70px;
    grid-area: 2 / 2 / 3 / 3;



    .price_property_full {
      padding-left: 15px;
      padding-top: 10px;
    }

    span {
      color: rgb(255, 255, 255);
      font-size: 18px;
    }

    .price_add_full {

      margin-left: 50px;
      cursor: pointer;
    }

    .price_add_full img {
      max-height: 20px;
      max-width: 20px;
    }

    .price_add_full img:hover {
      transform: scale(1.2)
    }
  }

  .price_full:hover {
    transform: skew(0);
  }


  

    .item {
      display: flex;
      align-items: center;
      justify-content: center;
      user-select: none;
    }
    .item-1 {
      background-color: rgb(7, 216, 192);
    }

    .item-2 {
      background-color: rgb(146, 7, 216);
    }

    .item-3 {
     background-color: rgb(216, 7, 7);
    }
  
}

