  font-family: "BOMBFONT TRIAL";
  src: url("../fonts/BOMBFONT TRIAL.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {

  font-family: "Streetlife";
  src: url("../fonts/Streetlife.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}